@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit}body{font-family:'Rubik', sans-serif}.layout{min-height:100vh;background:url("/background/flat2.jpg") no-repeat center;display:grid;place-content:center}.quote{width:100%;display:grid;grid-template-columns:1fr;place-content:center}.quote__text{width:100%;height:100%;text-align:center;color:#854e2a;font-size:21px;font-weight:700;padding:0 18px}.quote__text-character{margin-top:8px;font-size:20px;font-style:italic;color:#c26324}.quote__image{width:180px;height:auto;margin:0 auto}.next-quote{margin:26px auto 0;text-align:center}.next-quote__button{background:#b87446;color:wheat;width:110px;height:50px;font-size:18px;font-weight:500;border:solid;border-radius:28px}.next-quote__button:hover,.next-quote__button:focus,.next-quote__button:active{background:#a06137;transition:0.1s linear;cursor:pointer}@media (min-width: 600px){.layout{background:url("/background/unnamed.jpg") no-repeat center}.layout .quote{width:600px;height:260px;grid-template-columns:2fr 1fr;background:cornsilk;box-shadow:0 0 10px rgba(102,101,101,0.5)}.layout .quote__text{margin-top:45px;color:#546459;font-size:22px;font-style:italic}.layout .quote__text-character{margin-top:18px;font-size:20px;color:#90b49d}.layout .quote__image{width:180px}.next-quote{margin:36px auto 0}.next-quote__button{background:#e90856;color:cornsilk;width:160px;height:50px;font-size:20px}.next-quote__button:hover,.next-quote__button:focus,.next-quote__button:active{background:#ce074d}}@media screen and (min-width: 960px){.layout{background:url("/background/unnamed.jpg") no-repeat center}.layout .quote{width:940px;height:300px;grid-template-columns:2fr 1fr;background:cornsilk;box-shadow:0 0 10px rgba(102,101,101,0.5)}.layout .quote__text{margin-top:45px;color:#546459;font-size:28px;font-style:italic}.layout .quote__text-character{margin-top:18px;font-size:24px;color:#90b49d}.layout .quote__image{width:220px}.next-quote{margin:36px auto 0}.next-quote__button{background:#e90856;color:cornsilk;width:180px;height:60px;font-size:22px}.next-quote__button:hover,.next-quote__button:focus,.next-quote__button:active{background:#ce074d}}

